.financial-health .q1 .radio-container {
  width: 44%;
  float: left;
  margin: 0 70px 0 0;
  cursor: pointer;
  height: 40px;
  background: #cccccc;
  border-radius: 20px;
  border: 2px solid gray;
}
.financial-health .q1 .radio-container:last-child {
  margin: 0 0 0 0;
}
.financial-health .q1 input[type="radio"]:checked,
.financial-health .q1 input[type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px;
}
.financial-health .q1 input[type="radio"]:checked + label,
.financial-health .q1 input[type="radio"]:not(:checked) + label {
  position: relative;
  padding-left: 40px;
  cursor: pointer;
  line-height: 18px;
  font-size: 16px;
  font-weight: 500;
  display: inline-block;
  color: white;
  margin: 9px 0 0 30px;
}

.financial-health .q1 input[type="radio"]:checked + label:before,
.financial-health .q1 input[type="radio"]:not(:checked) + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
  border: 1px solid gray;
  border-radius: 100%;
  background: #fff;
}

.financial-health .q1 input[type="radio"]:checked + label:after,
.financial-health .q1 input[type="radio"]:not(:checked) + label:after {
  content: "";
  width: 12px;
  height: 12px;
  background: gray;
  position: absolute;
  top: 3px;
  left: 3px;
  border-radius: 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.financial-health .q1 input[type="radio"]:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
}

.financial-health .q1 input[type="radio"]:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

